*, :after, :before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  box-sizing: border-box;
}

.cat-container {
  margin: 20px;
}

.breed-select {
  margin-bottom: 10px;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.box h2, .box p {
  margin-bottom: 15px;
}

.is-hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #0d0101;
  animation: rotation 2s ease-in-out infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:after {
  border-color: #ff3d00;
  animation-delay: 1s;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

